.paragraph-l {
  // Position

  // block-model

  // Typography
  @extend %font-Cabin;
  @include text(
    $style: normal,
    $weight: 500,
    $size: var(--font-size-paragraph),
    $height: calc(var(--font-size-paragraph) * 1.2)
  );

  // Decoration
  color: var(--color-text-dark);

  // Animation

  // Various
}

.link {
  // Position

  // block-model
  width: inherit;
  height: inherit;
  // Typography
  @extend %font-Cabin;
  @include text(
    $size: var(--font-size-addText),
    $height: calc(var(--font-size-addText) * 1.2)
  );

  // Decoration
  color: var(--color-text-light);
  // Animation
  @extend %transition-basic;

  // Various
  &:hover,
  &:active {
    @extend %transition-basic;
    @include animation(
      $name: pulsation,
      $duration: 1s,
      $iteration-count: infinite,
      $direction: normal,
      $timing-function: ease,
      $delay: 0s
    );
  }

  &:hover {
    color: var(--color-text-dark);
    background-color: var(--color-highlight-light);
    border-radius: inherit;
  }

  &:active {
    color: var(--color-text-dark);
    background-color: var(--color-highlight-light);
    border-radius: inherit;
  }
}

